class Address extends Component {
    constructor() {
        super();
    }
    render(){
        const optionItems = stateNames.map((state) =>
            <option key={state.name} value={state.name}>{state.name}</option>
        );
        return (
            <div className="each-ip-div">
                <div className="ip-label heading-style">Address</div>
                <div className="ip-div-style">
                    <input className="ip-style equal-width" type="text" name="addressline1" placeholder="Address line 1"></input>
                    <input className="ip-style left-margin equal-width" type="text" name="city" placeholder="City"></input>
                    <select className="select-style left-margin equal-width" onChange={(e) => this.setState({selectedTeam: e.target.value, validationError: e.target.value === "Select State" ? "You must select your favourite team" : ""})}>
                        {optionItems}
                    </select>
                    <input className="ip-style left-margin equal-width zip-text" type="text" name="zip" placeholder="Zip"></input>
                </div>
            </div>
        )
    }
}

export default Address;